// Layout .l-
≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡*/
.l-container {
    margin: 0 auto;
    width: 1200px;
}

@media only screen and (max-width: 1200px) {
    .l-container {
        width: 100%;
        padding: 0 30px;
    }
}

@media only screen and (max-width: 812px) {
    .l-container {
        width: 100%;
        padding: 0 15px;
    }
}

.p-header {
    background: transparent;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 999;
}

.p-header.is-dark {
    background-color: #131722;
}

.p-header__content {
    padding: 15px 50px;
    min-height: 70px;
}

.p-header__logo {
    width: 60px;
}

.p-header__logo img {
    width: 100%;
    height: auto;
    max-height: 100%;
    object-fit: cover;
}

@media only screen and (max-width: 812px) {
    .p-header__cgnav {
        display: none !important;
    }
}

.p-header__cgnav li {
    margin: 0 20px;
}

.p-header__cgnav li a {
    font-size: 1.6rem;
    font-weight: bold;
    color: white;
    transition: all 0.6s;
    position: relative;
}

.p-header__cgnav li a::after {
    content: "";
    position: absolute;
    bottom: -2px;
    left: 0;
    width: 0%;
    height: 2px;
    background-color: #e90101;
}

.p-header__cgnav li a:hover {
    color: #e90101;
}

.p-header__cgnav li a:hover::after {
    transition: all 0.6s;
    width: 100%;
}

@media only screen and (max-width: 812px) {
    .p-header__actions {
        display: none !important;
    }
}

.p-header__actions p {
    font-size: 1.6rem;
    font-weight: bold;
    color: white;
    transition: all 0.6s;
    position: relative;
}

.p-header__actions p::after {
    content: "";
    position: absolute;
    bottom: -2px;
    left: 0;
    width: 0%;
    height: 2px;
    background-color: #e90101;
    transition: all 0.6s;
}

.p-header__search {
    margin-right: 20px;
}

.p-header__search svg {
    font-size: 1.8rem;
    color: white;
}

.p-header__search svg:hover {
    color: #e90101;
}

.p-header .icon-circle {
    width: 40px;
    height: 40px;
    border: 2px solid white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 10px;
}

.p-header .icon-circle svg {
    font-size: 1.8rem;
    color: white;
}

.p-header .icon-circle svg:hover {
    color: #e90101;
}

.p-header__account:hover {
    transition: all 0.6s;
}

.p-header__account:hover p {
    color: #e90101;
}

.p-header__account:hover p::after {
    width: 100%;
}

.p-header__account:hover .icon-circle {
    border-color: #e90101;
}

.p-header__account:hover svg {
    color: #e90101;
}

.p-top {
    position: relative;
    z-index: 1;
}

.p-top1 {
    padding: 100px 0;
