.c-mainvisuals {
    position: relative;
    height: 100vh;
}

.c-mainvisuals__content {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 1;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    pointer-events: all;
}

.p-video {
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    position: relative;
}

.p-video .l-container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.p-video__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.p-video__content {
    height: 90vh;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.p-video video {
    width: 1200px;
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
}
@media only screen and (max-width: 812px) {
    .p-video video {
        width: auto;
    }
}

.p-video__back {
    height: 50px;
    width: 150px;
    position: absolute;
    top: 100px;
    left: 0;
    transform: translateX(-50%);
}

@media only screen and (max-width: 812px) {
    .p-video__back {
        transform: unset;
    }
}

.p-video__back svg {
    display: inline-block;
    margin-right: 5px;
}

.p-footer {
    background: #0a0d14;
    position: relative;
    z-index: 1;
}

.p-footer__top {
    padding: 10px 0;
    border-bottom: 1px solid #ddd;
}

.p-footer__bottom {
    padding-bottom: 50px;
}

.p-footer__logo {
    width: 40px;
    height: 40px;
    display: block;
}

.p-footer__logo img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.p-footer__socials {
    padding: 10px 0;
    color: white;
    font-size: 1.6rem;
    margin-left: 15px;
}

@media only screen and (max-width: 450px) {
    .p-footer__socials {
        display: none;
    }
}

.p-footer__socials p {
    display: inline-block;
    padding-left: 2px;
    color: #949cb0;
    transition: all 0.6s;
}

.p-footer__socials:hover p {
    color: white;
}

.p-footer__item h3 {
    font-size: 1.8rem;
    color: white;
    font-weight: bold;
    margin: 50px 0 30px;
}

.p-footer__copyright {
    font-size: 1.2rem;
    letter-spacing: 0.64px;
    height: 40px;
    background-color: black;
    line-height: 40px;
    text-align: center;
    color: white;
}

.p-footer__breadcrumb a {
    font-size: 1.4rem;
    color: #949cb0;
    margin: 10px 0;
    display: block;
}

.p-footer__breadcrumb a:hover {
    color: white;
}

.c-fixed {
    position: fixed;
    width: 50%;
}

.c-absolute {
    position: absolute;
}

.p-movie1 {
