    }
}

@media only screen and (max-width: 812px) {
    .p-movie1__title {
        font-size: 5rem;
    }
}

.p-movie1__cat {
    margin-top: 10px;
    color: #e90101;
    font-size: 1.4rem;
}

.p-movie1__description {
    margin-top: 20px;
    font-size: 1.6rem;
    line-height: 1.7;
    letter-spacing: 0.64px;
    color: white;
}

@media only screen and (max-width: 1200px) {
    .p-movie1__description {
        font-size: 1.4rem;
    }
}

.p-movie1__actions .c-btn {
    margin-top: 30px;
}

@media only screen and (max-width: 1200px) {
    .p-movie1__actions .c-btn {
        font-size: 1.4rem;
    }
}

.p-movie1__actions .u-flex {
    margin-top: 30px;
}

.p-movie1__actions span {
    display: inline-flex;
    font-size: 1.4rem;
    color: white;
    margin-left: 20px;
    letter-spacing: 0.64px;
}

.p-movie1 .c-btn {
    width: 200px;
    height: 50px;
    border-radius: 999px;
}

@media only screen and (max-width: 450px) {
    .p-movie1 .c-btn {
        width: 100%;
        margin-bottom: 30px;
    }
}

.p-movie1 .c-icon {
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 20px;
    font-size: 2rem;
    background-color: #eaeaea;
    border-radius: 50%;
    color: #919191;
}

.p-movie1__image {
    width: 350px;
}

@media only screen and (max-width: 812px) {
    .p-movie1__image {
        width: 100%;
        margin-top: 30px;
    }
}

.p-movie1__image .gutter {
    border: 1px double #ddd;
    border-radius: 2px;
    padding: 25px;
    background-color: white;
}

.p-movie1__image img {
    width: 100%;
    height: auto;
    max-height: 100%;
}

.p-movie1__item {
    font-size: 1.5rem;
    margin-top: 20px;
}

.p-movie1__item--bold {
    font-weight: bold;
    min-width: 120px;
    display: inline-block;
}

.p-movie3 {
    padding: 100px 0;
    background-color: #131722;
}

.p-movie3 .c-item .gutter {
    height: 250px;
}

.p-movie3 .c-title {
    margin-top: 5px;
}

.p-movie3__slide {
    position: relative;
}

.p-movie3__content {
    margin-top: 60px;
    margin-left: -15px;
    margin-right: -15px;
}

.p-movie3 .slick-arrow {
    position: absolute;
    top: 50%;
    width: 40px;
    height: 40px;
    background: #eaeaea;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.6s ease;
}

.p-movie3 .slick-arrow:hover {
    background-color: #e90101;
}

.p-movie3 .p-movie3__next {
    right: 0;
    left: unset;
    transform: translate(50%, -50%);
}

.p-movie3 .p-movie3__prev {
    top: 50%;
    left: 0;
    transform: translate(-50%, -50%);
}

/*≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡
