≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡*/
/*≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡
*/
.c-slide {
    position: relative;
    height: 100%;
}

.c-slide .slick-slide > div {
    height: 100%;
}

.c-slide .slick-list {
    height: 100%;
}

.c-slide .slick-list .slick-track {
    height: 100%;
}

.c-slide__controller {
    display: flex;
    align-items: center;
    position: absolute;
    bottom: 30px;
    right: 20px;
    z-index: 2;
}

@media only screen and (max-width: 812px) {
    .c-slide__controller {
        right: unset;
        left: 0;
    }
}

.c-slide__item {
    height: 100%;
}

.c-slide__image {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.c-slide__info {
    position: absolute;
    bottom: 30px;
    left: 0;
    height: 100vh;
    z-index: 1;
    color: white;
    width: 100%;
    display: flex;
    align-items: flex-end;
}

@media only screen and (max-width: 812px) {
    .c-slide__info {
        bottom: 90px;
    }
}

.c-slide__info .l-container {
    padding-right: 40%;
}

@media only screen and (max-width: 812px) {
    .c-slide__info .l-container {
        padding-right: unset;
    }
}

.c-slide__label {
    display: block;
    position: relative;
    padding-left: 10px;
    font-size: 1.4rem;
    font-weight: bold;
    color: #ddd;
}

.c-slide__label::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 3px;
    height: 100%;
    background-color: #e90101;
}

.c-slide__title {
    font-size: 10rem;
    font-weight: bold;
}

@media only screen and (max-width: 1200px) {
    .c-slide__title {
        font-size: 8rem;
    }
}

@media only screen and (max-width: 812px) {
    .c-slide__title {
        font-size: 6rem;
    }
}

.c-slide__cat {
    margin-top: 10px;
    color: #e90101;
    font-size: 1.4rem;
}

.c-slide__description {
    margin-top: 20px;
    font-size: 1.6rem;
    line-height: 1.7;
    letter-spacing: 0.64px;
}

.c-slide__actions {
    margin-top: 40px;
    display: flex;
}

.c-slide__actions .c-btn {
    height: 50px;
    width: 250px;
    margin-right: 30px;
}

.c-slide__controller span,
.c-slide__controller p {
    display: block;
    font-size: 2rem;
    color: #fff;
}

.is-move .c-slide__border span {
    left: 100%;
    transition: all 0.7s;
}

.c-slide__number {
    width: 20px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    overflow: hidden;
    position: relative;
}

.c-slide__number span {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate3d(0, -100%, 0);
}

